import React, { Fragment, useState } from 'react';
import { Col, Container, Row } from 'reactstrap';
import { Breadcrumbs } from '../../../../AbstractElements';
import MyProfileEdit from './EditmyProfile';
import RegisterUser from './RegisterUser';
import AdminTable from './AdminTable';

const AdminEditContain = () => {
  const [toggle, setToggle] = useState(false);
  const showUserTable = JSON.parse(localStorage.getItem('user')).role === 'admin';

  return (
    <Fragment>
      <Breadcrumbs mainTitle='View' parent='Profile' title='View' />
      <Container fluid={true}>
        <div className='edit-profile'>
          <Row>
            <Col md='12' xl='4'>
              <MyProfileEdit/>
            </Col>
            {showUserTable?
              <>
                <Col md='4' xl='4'>
                  <RegisterUser render={()=>setToggle(!toggle)} />
                </Col>
                <Col md='8' xl='4'>
                  <AdminTable toggle={toggle} />
                </Col>
              </>:<></>
            }
          </Row>
        </div>
      </Container>
    </Fragment>
  );
};
export default AdminEditContain;
