import React, { Fragment } from 'react';
import { useForm } from 'react-hook-form';
import { Form, FormGroup, Label, Button, Modal, ModalBody, ModalHeader, Row } from 'reactstrap';
import { Btn } from '../../../../AbstractElements';
import { bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';

const PaymentsExportModal = (props) => {
    const { register, handleSubmit, formState: { errors }, reset } = useForm();
    const onSubmit = (data) => {
        if (data !== '') {
            const requestOptions = {
                method: 'GET',
                headers: {  ...bearerAuthHeader() }
            };

            return fetch(`${ApiHost}/api/payments/csv?start=${data.start}&end=${data.end}`, requestOptions)
                .then((response) => {
                    console.log(response)
                    return response.blob()
                })
                .then((blob) => {
                    const url = window.URL.createObjectURL(
                        new Blob([blob]),
                    );

                    const link = document.createElement('a');
                    link.href = url;
                    link.setAttribute(
                        'download',
                        `payments.csv`,
                    );

                    // Append to html link element page
                    document.body.appendChild(link);

                    // Start download
                    link.click();

                    // Clean up and remove the link
                    link.parentNode.removeChild(link);

                    props.addToggle();
                    reset();
                });
        } else {
            errors.showMessages();
        }
    };

    return (
        <Fragment>
            <Modal className="modal-bookmark" isOpen={props.value} toggle={props.addToggle} size="md">
                <ModalHeader>
                    {'Export Payments as CSV'}
                    <Button color="transparent" className="btn-close float-end" onClick={props.addToggle} type="button" data-bs-dismiss="modal" aria-label="Close">
                    </Button>
                </ModalHeader>
                <ModalBody>
                    <Form
                        className="form-bookmark needs-validation"
                        onSubmit={handleSubmit(onSubmit)} >
                        <div className="form-row">
                            <FormGroup className="col-md-12">
                                <Row>
                                    <FormGroup className="col-md-6">
                                        <Label>{'Start Date'}</Label>
                                        <input
                                            className="form-control"
                                            type="date"
                                            autoComplete="off"
                                            {...register('start')}
                                        />
                                    </FormGroup>
                                    <FormGroup className="col-md-6">
                                        <Label>{'End Date'}</Label>
                                        <input
                                            className="form-control"
                                            type="date"
                                            autoComplete="off"
                                            {...register('end')}
                                        />
                                    </FormGroup>
                                </Row>
                            </FormGroup>
                        </div>
                        <Btn attrBtn={{ color: 'secondary', type: 'submit' }} >{'Export'}</Btn>
                    </Form>
                </ModalBody>
            </Modal>
        </Fragment>
    );
};
export default PaymentsExportModal;