import React, { Fragment, useEffect, useState } from 'react';
import Info from './Info';
import { Container, Row } from 'reactstrap';
import { Breadcrumbs } from '../../../../AbstractElements';
import { useParams } from 'react-router';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import AddCoinToPlace from './AddCoinToPlace';
import CoinsForPlaceTable from './CoinsForPlaceTable';

const EditPlace = () => {
    const {id} = useParams();
    const [data, setData] = useState({});
    const [coins, setCoins] = useState([]);
    const [typeOptions, setTypeOptions] = useState([]);
    const [update, setUpdate] = useState(false);

    const headers = { 'Content-Type': 'application/json', ...bearerAuthHeader()}

    useEffect(()=>{
        fetch(ApiHost + '/api/coins/places/' + id, {method: 'GET', headers})
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                    setData(res);
                }else{
                    toast.error(res.error);
                }
            });

        fetch(ApiHost + '/api/coins/types?place=' + id, {method: 'GET', headers})
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                    setCoins(res);
                }else{
                    toast.error(res.error);
                }
            });
    }, [update]);

    useEffect(()=>{
        fetch(ApiHost + '/api/coins/types?exclude=' + coins.map((e)=>e.id).toString(), {method: 'GET', headers})
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                    setTypeOptions(res);
                }else{
                    toast.error(res.error);
                }
            });
    }, [coins])

    return (
        <Fragment>
            <Breadcrumbs parent="Places" title="Edit Place" mainTitle={`Place: ${data.id}`} />
            <Container fluid={true} className="credit-card">
                <Row>
                    <Info data={data} />
                    <AddCoinToPlace update={()=>setUpdate(!update)} coins={typeOptions}/>
                    <CoinsForPlaceTable data={coins} update={()=>setUpdate(!update)} />
                </Row>
            </Container>
        </Fragment>
    );
};
export default EditPlace;