import React, { Fragment, useContext, useEffect, useState } from 'react';
import { Coins } from '../../../../Constant';
import CoinsLoop from './CoinsLoop';
import GridClass from './Common/GridClass';
import { Card, CardBody, TabContent, TabPane } from 'reactstrap';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import CoinContext from '../../../../_helper/Coin';
import { handleResponseAndCheck, bearerAuthHeader } from '../../../../Services/fack.backend';
import PairsTable from './PairsTable';
import PairModal from './ModalPair';

const CoinTabs = () => {
  const { updateCoins, toggleCoin, activeTabss } = useContext(CoinContext);

  const [coins, setCoins] = useState([]);
  const [coinToTypes, setCoinToTypes] = useState([]);
  const [pairs, setPairs] = useState([]);
  const [filter, setFilter] = useState({ });
  const [pairFilter, setPairFilter] = useState({ join: 'to,from' });

  const headers = () => ({ 'Content-Type': 'application/json', ...bearerAuthHeader()})

  useEffect(()=>{
    getCoinsToTypes();
  }, []);

  useEffect(()=>{
    const timeOutId = setTimeout(() => getCoins(), 500);
    return () => clearTimeout(timeOutId);
  }, [toggleCoin, filter]);

  useEffect(()=>{
    const timeOutId = setTimeout(() => getPairs(), 500);
    return () => clearTimeout(timeOutId);
  }, [pairFilter]);

  const getPairs = () =>{
    fetch(`${ApiHost}/api/coins/pairs?${Object.keys(pairFilter).map((key)=>{return key + '=' + pairFilter[key]}).join('&')}`, {method: 'GET', headers: headers()})
    .then(handleResponseAndCheck)
    .then((res) => {
        if(!res.error){
          setPairs(res);
        }else{
          toast.error(res.error);
        }
    });
  }

  const getCoins = () =>{
    fetch(`${ApiHost}/api/coins/?${Object.keys(filter).map((key)=>{return key + '=' + filter[key]}).join('&')}`, {method: 'GET', headers: headers()})
      .then(handleResponseAndCheck)
      .then((res) => {
          if(!res.error){
              setCoins(res);
          }else{
              toast.error(res.error);
          }
      });
  }

  const getCoinsToTypes = () =>{
    fetch(`${ApiHost}/api/coins/types`, {method: 'GET', headers: headers()})
      .then(handleResponseAndCheck)
      .then((res) => {
          if(!res.error){
              setCoinToTypes(res);
          }else{
              toast.error(res.error);
          }
      });
  }

  const removeCoin = (id, callBack) =>{
    fetch(ApiHost + '/api/coins/' + id, {method: 'DELETE', headers: headers()})
      .then(handleResponseAndCheck)
      .then((res) => {
          if(!res.error){
            updateCoins();
          }

          callBack(res.error);
      });
  }

  const handleChange = (key, value) =>{
    let _filter = {...filter};

    _filter[key] = value;

    if(key == 'popular' && !value)
      delete _filter[key];

    setFilter(_filter);
  }

  const handlePairChange = (key, value) =>{
    let _filter = {...pairFilter};

    _filter[key] = value;

    setPairFilter(_filter);
  }

  return (
    <Fragment>
      <TabContent activeTab={activeTabss}>
        <TabPane tabId='1'>
          <Card className='mb-0'>
            <GridClass title={Coins}/>
            <CardBody className='pb-0'>
              <div className={`details-bookmark`}>
                <CoinsLoop setFilter={handleChange} data={coins} remove={removeCoin} />
              </div>
            </CardBody>
          </Card>
        </TabPane>
        <TabPane tabId='2'>
          <Card className='mb-0'>
            <GridClass title={"Pairs"}/>
            <CardBody>
              <div className={`details-bookmark text-center`}>
                <PairsTable data={pairs} coinToTypes={coinToTypes} setFilter={handlePairChange} />
              </div>
            </CardBody>
          </Card>
        </TabPane>
        <PairModal/>
      </TabContent>
    </Fragment>
  );
};
export default CoinTabs;
