import React, { Fragment, useContext, useEffect } from 'react';
import { Card, CardBody, CardHeader, Form, FormGroup, Input, Label, Row } from 'reactstrap';
import { H5, H4, H6, P, Image, Btn } from '../../../../AbstractElements';
import { Title, orderStatuses } from '../../../../Constant';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import { useForm } from 'react-hook-form';

const OrderInfoForm = ({ order, update }) => {
  const { register, handleSubmit, reset, formState: { errors }, setValue } = useForm();

  const onEditSubmit = (data) => {
    const requestOptions = {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()},
      body: JSON.stringify(data),
    };
    order.orderStatus = order.orderStatus;

    return fetch(ApiHost + '/api/users/orders/' + order.id + '/status', requestOptions)
      .then(handleResponseAndCheck)
      .then((res) => {
        if(!res.error){
          toast.success("Status updated!");
        }else{
          toast.error(res.error);
        }
      });
  };

  useEffect(()=>{
    setValue('orderStatus', order.orderStatus);
  });

  return (
    <Fragment>
      <Card>
        <CardHeader>
          <H4 attrH4={{ className: 'card-title mb-0' }}>{'Order Number: ' + order.orderNumber}</H4>
          <div className='card-options'>
            <a className='card-options-collapse' href='#javascript'>
              <i className='fe fe-chevron-up'></i>
            </a>
            <a className='card-options-remove' href='#javascript'>
              <i className='fe fe-x'></i>
            </a>
          </div>
        </CardHeader>
        <CardBody>
          <Form onSubmit={handleSubmit((data) => onEditSubmit(data))}>
            {/* <FormGroup className='col-md-12'>
              <Label className='form-label'>{Title}</Label>
              <Input className='form-control' defaultValue={payment.title} disabled />
            </FormGroup> */}
            <FormGroup className='col-md-12'>
              <Row>
                <FormGroup className='col-md-4'>
                  <Label className='form-label'>{'From'}</Label>
                  <Input className='form-control' type='text' defaultValue={order.pair.from.name_en} disabled />
                </FormGroup>
                <FormGroup className='col-md-4'>
                  <Label className='form-label'>{'To'}</Label>
                  <Input className='form-control' type='text' defaultValue={order.pair.to.name_en} disabled />
                </FormGroup>
                <FormGroup className='col-md-4'>
                  <Label className='form-label'>{'Amount'}</Label>
                  <Input className='form-control' defaultValue={order.amount} disabled />
                </FormGroup>
                <FormGroup className='col-md-4'>
                  <Label className='form-label'>{'Option'}</Label>
                  <Input className='form-control' defaultValue={order.option ? 'Take' : 'Give'} disabled />
                </FormGroup>
                {order.place.name_en && <FormGroup className='col-md-6'>
                  <Label className='form-label'>{'Place'}</Label>
                  <Input className='form-control' defaultValue={order.place.name_en} disabled />
                </FormGroup>}
                <FormGroup className='col-md-4'>
                  <Label className='form-label'>{'Status'}</Label>
                  <select type="select" {...register("orderStatus", { required: true })} className="form-control">
                      {orderStatuses.map((item, i) =>
                          <option key={i} value={item.toLocaleLowerCase()} disabled={i === 0?true:false}>{item}</option>
                      )}
                  </select>
                </FormGroup>
                <FormGroup className='col-md-6'>
                  <Label className='form-label'>{'Created At'}</Label>
                  <Input className='form-control' defaultValue={order.createdAt} disabled />
                </FormGroup>
              </Row>
            </FormGroup>
            <div className='form-footer d-block mb-2'>
              <Btn attrBtn={{ className: 'btn-block', color: 'secondary' }}>{'Change Status'}</Btn>
            </div>
          </Form>
        </CardBody>
      </Card>
    </Fragment>
  );
};
export default OrderInfoForm;
