import React, { Fragment, useEffect, useState } from 'react';
import Info from './Info';
import { Container, Row } from 'reactstrap';
import { Breadcrumbs } from '../../../../AbstractElements';
import { useParams } from 'react-router';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import AddPair from './AddPair';
import Pairs from './Pairs';
import PlacesForCoinTable from './PlacesForCoinTable';
import PairModal from '../CoinsAndTypes/ModalPair';

const EditCoinType = () => {
    const {coinId, id} = useParams();
    const [data, setData] = useState({});
    const [typeOptions, setTypeOptions] = useState([]);
    const [pairs, setPairs] = useState([]);
    const [places, setPlaces] = useState([]);
    const [update, setUpdate] = useState(false);

    const headers = { 'Content-Type': 'application/json', ...bearerAuthHeader()}

    useEffect(()=>{
        fetch(ApiHost + '/api/coins/types/' + id, {method: 'GET', headers})
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                    setData(res);
                }else{
                    toast.error(res.error);
                }
            });
    }, [update]);

    useEffect(()=>{
        fetch(ApiHost + '/api/coins/types/' + id + '/pairs?join=to', {method: 'GET', headers})
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                    setPairs(res);
                }else{
                    toast.error(res.error);
                }
            });
    }, [data])

    useEffect(()=>{
        let ids = pairs.map((e)=>e.toId);
        fetch(ApiHost + '/api/coins/types?exclude=' + ids.toString(), {method: 'GET', headers})
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                    setTypeOptions(res);
                }else{
                    toast.error(res.error);
                }
            });
    }, [pairs])

    useEffect(()=>{
        fetch(ApiHost + '/api/coins/places?coinToTypeId=' + id, {method: 'GET', headers})
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                    setPlaces(res);
                }else{
                    toast.error(res.error);
                }
            });
    }, []);

    return (
        <Fragment>
            <Breadcrumbs parent="Coins" title="Edit Coin with Type" mainTitle={`${data.name_en}`} />
            <Container fluid={true} className="credit-card">
                <Row>
                    <Info coinType={data} update={()=>{setUpdate(!update)}}/>
                    <AddPair coinType={data} typeOptions={typeOptions} update={()=>{setUpdate(!update)}}/>
                    <Pairs data={pairs} update={()=>{setUpdate(!update)}}/>
                    <PlacesForCoinTable data={places}/>
                </Row>
                <PairModal/>
            </Container>
        </Fragment>
    );
};
export default EditCoinType;