import React, { Fragment, useEffect, useState } from 'react';
import Info from './Info';
import { Card, Col, Container, Nav, Row } from 'reactstrap';
import { Breadcrumbs } from '../../../../AbstractElements';
import { useParams } from 'react-router';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import { ArrowLeft, PlusCircle } from 'react-feather';
import AddCoinWithType from './AddCoinWithType';
import { coinTypes } from '../../../../Constant';
import { Link } from 'react-router-dom';
  

const CoinInfo = () => {
    const {id} = useParams();
    const [data, setData] = useState({coinToTypes: [], name: ''});
    const [update, setUpdate] = useState(false);
    const [addModal, setAddModal] = useState(false);
    const [types, setTypes] = useState([]);

    const headers = { 'Content-Type': 'application/json', ...bearerAuthHeader()}

    useEffect(()=>{
        fetch(ApiHost + '/api/coins/' + id, {method: 'GET', headers})
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                    setData(res);
                }else{
                    toast.error(res.error);
                }
            });
    }, [update]);

    useEffect(()=>{
        let coinToTypes = data.coinToTypes.map((e)=>e.type);
        setTypes(coinTypes.filter(type => coinToTypes.indexOf(type) === -1));
    }, [data]);

    return (
        <Fragment>
            <Breadcrumbs parent="Coins" title="Coin Info" mainTitle={`${data.name}`} />
            <Container fluid={true} className="credit-card">
                <Row>
                    <Col md="12" className="project-list">
                        <Card>
                            <Row>
                                <Col md="6">
                                    <div style={{width: 'fit-content'}}>
                                        <Link className="btn btn-primary" style={{ color: 'white' }} to={`${process.env.PUBLIC_URL}/coins`}> <ArrowLeft /></Link>
                                    </div>
                                </Col>
                                <Col md="6">
                                    <div className="text-end">
                                        <div className="btn btn-primary" style={{ color: 'white' }} onClick={()=>setAddModal(!addModal)}> <PlusCircle />{"Add Coin with Type"}</div>
                                    </div>
                                </Col>
                            </Row>
                        </Card>
                    </Col>
                    <Info coin={data} update={()=>{setUpdate(!update)}}/>
                </Row>
            </Container>
            <AddCoinWithType types={types} value={addModal} addToggle={()=>setAddModal(!addModal)} update={()=>{setUpdate(!update)}} />
        </Fragment>
    );
};
export default CoinInfo;