import React, { Fragment, useEffect, useState } from 'react';
import { NoCoinFound } from '../../../../Constant';
import { Row, Card, Col, Label, FormGroup } from 'reactstrap';
import SweetAlert from 'sweetalert2';
import { H6, P, UL, LI } from '../../../../AbstractElements';
import { ApiHost } from '../../../../Config/Config';
import { useNavigate } from 'react-router';

const DataLoop = ({data, remove, setFilter}) => {
  const navigate = useNavigate();
  const [query, setQuery] = useState("");

  const OnHandleClick = (data) => {
    navigate('/coins/' + data.id)
  };

  const removeCoin = (id) => {
    SweetAlert.fire({
        title: 'Are you sure?',
        text: 'Once deleted, you will not be able to recover this Coin!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Ok',
        cancelButtonText: 'cancel',
        reverseButtons: true,
    }).then((result) => {
        if (result.value) {
            remove(id, (error)=>{
                if(!error)
                    SweetAlert.fire('Deleted!', 'This Coin has been deleted.', 'success');
                else
                    SweetAlert.fire('Error!', error, 'warning');
            });
        }
    });
  };

  useEffect(() => {
    const timeOutId = setTimeout(() => setFilter('name', query), 500);
    return () => clearTimeout(timeOutId);
  }, [query]);


  return (
    <Fragment>
      <Row className='mb-4'>
        <FormGroup className="col-3">
            <Label>Name</Label>
            <input name="query" type='text' className="form-control" autoComplete="off" onChange={(e)=>setQuery(e.currentTarget.value)} />
        </FormGroup>
      </Row>
      <Row>
        {data.length > 0 ? (
          data.map((coin) => {
            return (
              <Col xxl='3' md='4' className='col-ed-4' key={coin.id}>
                <Card className='bookmark-card o-hidden'>
                  <div className='details-website'>
                    <div className='desciption-data'>
                      <div className='title-bookmark'>
                        <H6> {coin.name} </H6>
                        <div className='hover-block'>
                          <UL attrUL={{ className: 'simple-list flex-row' }}>
                            <LI attrLI={{ className: 'border-0 pointer' }}>
                              <a className='theme-text' onClick={() => OnHandleClick(coin)}>
                                <i className='fa fa-edit'></i>
                              </a>
                            </LI>
                            <LI attrLI={{ className: 'border-0 pointer' }}>
                              <a className='theme-text' onClick={() => removeCoin(coin.id)}>
                                <i className='fa fa-trash-o'></i>
                              </a>
                            </LI>
                          </UL>
                        </div>
                      </div>
                    </div>
                  </div>
                </Card>
              </Col>
            );
          })
        ) : (
          <Col xl='12'>
            <div className='no-favourite'>
              <span>{NoCoinFound}</span>
            </div>
          </Col>
        )}
      </Row>
    </Fragment>
  );
};
export default DataLoop;
