import { Image, Btn } from '../../../../AbstractElements';
import { CreditCard, Submit } from '../../../../Constant';
import card from '../../../../assets/images/ecommerce/card.png';
import { Card, CardBody, Col, Form, FormGroup, Input, Label, Row } from 'reactstrap';
import React, { Fragment } from 'react';
import HeaderCard from '../../../Common/Component/HeaderCard';
import { useForm } from 'react-hook-form';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import { useParams } from 'react-router';

const AddCoinToPlace = ({coins, update}) => {
  const {id} = useParams();
  const { register, handleSubmit, formState: { errors }, reset } = useForm();

  const onSubmit = (data) => {
      if (data !== '') {
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()},
            body: JSON.stringify({coinToTypeId: data.coinToTypeId}),
        };
    
        return fetch(ApiHost + '/api/coins/places/' + id, requestOptions)
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                    update();
                    reset();
                }else{
                    toast.error(res.error);
                }
            });
      } else {
          errors.showMessages();
      }
  };

  return (
    <Fragment>
      <Col lg='6' xxl='6' className='box-col-12'>
        <Card className='height-equal credit-form card'>
          <HeaderCard title={'Add Coin to Place'} />
          <CardBody>
            <Row>
              <Form onSubmit={handleSubmit(onSubmit)}  className='theme-form mega-form'>
                <FormGroup className="col-md-12">
                  <Label>{'Coin With Type'}</Label>
                  <select className='form-select form-control' size='1' {...register('coinToTypeId', { required: true })}>
                    {coins.map((coin)=>{
                      return <option key={coin.id} value={coin.id}>{coin.name_en}</option>
                    })}
                  </select>
                    <span style={{ color: 'red' }}>
                        {errors.id && 'Coin With Type is required'}
                    </span>
                </FormGroup>
                <div className='col-12'>
                  <Btn attrBtn={{ color: 'primary', className: 'btn btn-primary', type: 'submit' }}>{Submit}</Btn>
                </div>
              </Form>
            </Row>
          </CardBody>
        </Card>
      </Col>
    </Fragment>
  );
};
export default AddCoinToPlace;
