import React, { Fragment, useEffect } from 'react';
import { Btn } from '../../../../AbstractElements';
import { DebitCard, ExpirationDate, SelectMonth, SelectYear, Submit } from '../../../../Constant';
import { Card, CardBody, Col, FormGroup, Input, Row, Label } from 'reactstrap';
import HeaderCard from '../../../Common/Component/HeaderCard';
import { useForm } from 'react-hook-form';
import { ApiHost } from '../../../../Config/Config';
import { handleResponseAndCheck, bearerAuthHeader } from '../../../../Services/fack.backend';
import { toast } from 'react-toastify';

const Info = ({data}) => {
  const { register, handleSubmit, formState: { errors }, setValue } = useForm();

  useEffect(()=>{
    setValue('name_en', data.name_en);
    setValue('name_am', data.name_am);
    setValue('name_ru', data.name_ru);
  }, [data]);

  const onSubmit1 = (formData) => {
    if (formData !== '') {
        const requestOptions = {
          method: 'PUT',
          headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()},
          body: JSON.stringify(formData),
        };
    
        return fetch(ApiHost + '/api/coins/places/' + data.id, requestOptions)
          .then(handleResponseAndCheck)
          .then((res) => {
              if(!res.error){
                toast.success('Updated!');
              }else{
                toast.error(res.error);
              }
          });
    } else {
        errors.showMessages();
    }
  };
  return (
    <Fragment>
      <Col lg='6' xxl='6' className='box-col-6'>
        <Card className='height-equal'>
          <HeaderCard title={'Place Info'} />
          <CardBody>
            <form
              className="form-bookmark needs-validation"
              onSubmit={handleSubmit(onSubmit1)} >
              <div className="form-row">
                <Row className='theme-form e-commerce-form'>
                  <FormGroup className="col-md-6">
                      <Label>{"Name EN"}</Label>
                      <input
                          className="form-control"
                          type="text"
                          autoComplete="off"
                          {...register('name_en', { required: true })}
                      />
                      <span style={{ color: 'red' }}>
                          {errors.name_en && 'Name EN is required'}
                      </span>
                  </FormGroup>
                  <FormGroup className="col-md-6">
                      <Label>{"Name AM"}</Label>
                      <input
                          className="form-control"
                          type="text"
                          autoComplete="off"
                          {...register('name_am', { required: true })}
                      />
                      <span style={{ color: 'red' }}>
                          {errors.name_am && 'Name AM is required'}
                      </span>
                  </FormGroup>
                  <FormGroup className="col-md-6">
                      <Label>{"Name RU"}</Label>
                      <input
                          className="form-control"
                          type="text"
                          autoComplete="off"
                          {...register('name_ru', { required: true })}
                      />
                      <span style={{ color: 'red' }}>
                          {errors.name_ru && 'Name RU is required'}
                      </span>
                  </FormGroup>
                </Row>
              </div>
              <Btn attrBtn={{ color: 'primary', type: 'submit' }} >{"Save"}</Btn>&nbsp;&nbsp;
            </form>
          </CardBody>
        </Card>
      </Col>
    </Fragment>
  );
};
export default Info;
