import React, { Fragment, useState, useEffect, useCallback } from 'react';
import { Container, Row, Col, Card, CardBody, Label, FormGroup } from 'reactstrap';
import { Breadcrumbs, Btn } from '../../../../AbstractElements';
import HeaderCard from '../../../Common/Component/HeaderCard';
import DataTableComponent from './DataTableComponent';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import { NewApartment } from '../../../../Constant';
import PlaceModal from './ModalPlace';

const PlacesDataTable = () => {
  const [places, setPlaces] = useState([]);
  const [updateData, setUpdateData] = useState(false);
  const [addModal, setAddModal] = useState(false);
  const [query, setQuery] = useState("");


  const addToggle = () => {
    setAddModal(!addModal);
  };

  const addToggleCallback = useCallback((toggle) => {
    setAddModal(toggle);
  });

  const headers = () => ({ 'Content-Type': 'application/json', ...bearerAuthHeader()})

  const getData = (query) =>{
    fetch(`${ApiHost}/api/coins/places/${query ? '?query=' + query : ''}`, {method: 'GET', headers: headers()})
      .then(handleResponseAndCheck)
      .then((res) => {
          if(!res.error){
            setPlaces(res);
          }else{
            toast.error(res.error);
          }
      });
  }

  useEffect(()=>{
    getData();
  }, [updateData]);

  useEffect(() => {
    const timeOutId = setTimeout(() => getData(query), 500);
    return () => clearTimeout(timeOutId);
  }, [query]);

  return (
    <Fragment>
      <Breadcrumbs parent="Places" title="Places Table" mainTitle="Places Table" />
      <Container fluid={true}>
        <Row>
          <Col sm="12">
            <Card>
              <HeaderCard title="Select and Delete Data" span1={
                <Btn attrBtn={{ className: 'btn-mail ml-auto d-block w-10 btn-outline-primary', color: '', onClick: addToggle }}>
                    {'New Place'}
                  </Btn>
                } span2={
                  <FormGroup className="col-md-5">
                      <Label>Search</Label>
                      <input name="query" type='text' value={query} className="form-control" autoComplete="off" onChange={(e)=>setQuery(e.currentTarget.value)} />
                  </FormGroup>
                } />
              <CardBody>
                <DataTableComponent update={()=>setUpdateData(!updateData)} data={places} />
              </CardBody>
            </Card>
          </Col>
        </Row>
        <PlaceModal update={()=>setUpdateData(!updateData)} value={addModal} addToggle={addToggle} addToggleCallback={addToggleCallback} />
      </Container>
    </Fragment>
  );

};

export default PlacesDataTable;