import React, { Fragment, useEffect, useState } from 'react';
import { Form, useForm } from 'react-hook-form';
import { Btn, Image } from '../../../../AbstractElements';
import { DebitCard, Description, ExpirationDate, Name, Save, SelectMonth, SelectYear, Submit } from '../../../../Constant';
import { Card, CardBody, Col, FormGroup, Input, Row, Label, Media } from 'reactstrap';
import HeaderCard from '../../../Common/Component/HeaderCard';
import { handleResponseAndCheck, bearerAuthHeader } from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";

const Info = ({coinType, update}) => {
  const { register, handleSubmit, formState: { errors }, setValue } = useForm();

  useEffect(()=>{
    setValue('name_en', coinType.name_en)
    setValue('name_ru', coinType.name_ru)
    setValue('name_am', coinType.name_am)
    setValue('count', coinType.count)
  }, [coinType])

  const handleUpdate = (data) => {
      if (data !== '') {
        const requestOptions = {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()},
            body: JSON.stringify(data),
        };

        return fetch(ApiHost + '/api/coins/types/' + coinType.id, requestOptions)
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                  update();
                  toast.success('Updated!');
                }else{
                    toast.error(res.error);
                }
            });
      } else {
        errors.showMessages();
      }
  };

  return (
    <Fragment>
      <Col lg='8' xxl='8' className='box-col-6 debit-card'>
        <Card className='height-equal'>
          <HeaderCard title={'Coin Info'} />
          <CardBody>
            <form onSubmit={handleSubmit(handleUpdate)}>
              <Row className='e-commerce-form'>
                <FormGroup className="col-md-12">
                    <Row>
                        <FormGroup className="col-md-4">
                            <Label>{Name + ' EN'}</Label>
                            <input
                                className="form-control"
                                type="text"
                                autoComplete="off"
                                {...register('name_en', { required: true })}
                            />
                            <span style={{ color: 'red' }}>
                                {errors.name_en && 'Name EN is required'}
                            </span>
                        </FormGroup>
                        <FormGroup className="col-md-4">
                            <Label>{Name + ' RU'}</Label>
                            <input
                                className="form-control"
                                type="text"
                                autoComplete="off"
                                {...register('name_ru', { required: true })}
                            />
                            <span style={{ color: 'red' }}>
                                {errors.name_ru && 'Name RU is required'}
                            </span>
                        </FormGroup>
                        <FormGroup className="col-md-4">
                            <Label>{Name + ' AM'}</Label>
                            <input
                                className="form-control"
                                type="text"
                                autoComplete="off"
                                {...register('name_am', { required: true })}
                            />
                            <span style={{ color: 'red' }}>
                                {errors.name_am && 'Name AM is required'}
                            </span>
                        </FormGroup>
                    </Row>
                </FormGroup>
                <FormGroup className="col-md-12">
                  <Row>
                      <FormGroup className="col-md-4">
                          <Label>{'Count'}</Label>
                          <input
                              className="form-control"
                              type="number"
                              autoComplete="off"
                              {...register('count', { required: true })}
                          />
                          <span style={{ color: 'red' }}>
                              {errors.count && 'Count EN is required'}
                          </span>
                      </FormGroup>
                      <FormGroup className="col-md-4">
                          <Label>{'Type'}</Label>
                          <input
                              className="form-control"
                              type="text"
                              autoComplete="off"
                              defaultValue={coinType.type}
                              disabled
                          />
                      </FormGroup>
                  </Row>
                </FormGroup>
              </Row>
              <Btn attrBtn={{ color: 'primary', type: 'submit' }} >{Save}</Btn>
            </form>
          </CardBody>
        </Card>
      </Col>
    </Fragment>
  );
};
export default Info;
