import React, { Fragment, useContext } from 'react';
import { useForm } from 'react-hook-form';
import { Form, FormGroup, Label, Button, Modal, ModalBody, ModalHeader, Row } from 'reactstrap';
import { Btn } from '../../../../AbstractElements';
import { AddApartment, AddBookmark, AddBuildingService, Apartment, Building, Cancel, Collection, Description, General, Group, MyBookmarks, Name, Save, Title, WebUrl } from '../../../../Constant';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";

const PlaceModal = (props) => {
    const { register, handleSubmit, formState: { errors }, reset } = useForm();
    const onSubmit1 = (data) => {
        if (data !== '') {
            const requestOptions = {
                method: 'POST',
                headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()},
                body: JSON.stringify(data),
            };
        
            return fetch(ApiHost + '/api/coins/places', requestOptions)
                .then(handleResponseAndCheck)
                .then((res) => {
                    if(!res.error){
                        reset();
                        props.update();
                        props.addToggleCallback(props.addToggle);
                    }else{
                        toast.error(res.error);
                    }
                });
        } else {
            errors.showMessages();
        }
    };
    return (
        <Fragment>
            <Modal className="modal-bookmark" isOpen={props.value} toggle={props.addToggle} size="lg">
                <ModalHeader>
                    {'Add Place'}
                    <Button color="transparent" className="btn-close float-end" onClick={props.addToggle} type="button" data-bs-dismiss="modal" aria-label="Close">
                    </Button>
                </ModalHeader>
                <ModalBody>
                    <Form
                        className="form-bookmark needs-validation"
                        onSubmit={handleSubmit(onSubmit1)} >
                        <div className="form-row">
                            <FormGroup className="col-md-12">
                                <Row>
                                    <FormGroup className="col-md-6">
                                        <Label>{"Name EN"}</Label>
                                        <input
                                            className="form-control"
                                            type="text"
                                            autoComplete="off"
                                            {...register('name_en', { required: true })}
                                        />
                                        <span style={{ color: 'red' }}>
                                            {errors.name_en && 'Name EN is required'}
                                        </span>
                                    </FormGroup>
                                    <FormGroup className="col-md-6">
                                        <Label>{"Name AM"}</Label>
                                        <input
                                            className="form-control"
                                            type="text"
                                            autoComplete="off"
                                            {...register('name_am', { required: true })}
                                        />
                                        <span style={{ color: 'red' }}>
                                            {errors.name_am && 'Name AM is required'}
                                        </span>
                                    </FormGroup>
                                    <FormGroup className="col-md-6">
                                        <Label>{"Name RU"}</Label>
                                        <input
                                            className="form-control"
                                            type="text"
                                            autoComplete="off"
                                            {...register('name_ru', { required: true })}
                                        />
                                        <span style={{ color: 'red' }}>
                                            {errors.name_ru && 'Name RU is required'}
                                        </span>
                                    </FormGroup>
                                </Row>
                            </FormGroup>
                        </div>
                        <Btn attrBtn={{ color: 'secondary', type: 'submit' }} >{Save}</Btn>&nbsp;&nbsp;
                        <Btn attrBtn={{ color: 'primary', onClick: props.addToggle }} >{Cancel}</Btn>
                    </Form>
                </ModalBody>
            </Modal>
        </Fragment>
    );
};
export default PlaceModal;