import React, { useEffect, useState } from 'react';
import { Navigate, Outlet } from 'react-router-dom';
import { bearerAuthHeader, handleResponseAndCheck } from '../Services/fack.backend';

import { ApiHost } from '../Config/Config';
import axios from 'axios';

const PrivateRoute = () => {
  const [login, setLogin] = useState(localStorage.getItem("refresh_token") !== null);

  useEffect(() => {
    function checkUserData() {
      const requestOptions = {
        method: 'GET',
        headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()}
      };
  
      fetch(ApiHost + '/api/admins/current', requestOptions).then(handleResponseAndCheck)
        .then(()=>{
          setLogin(localStorage.getItem("refresh_token") !== null);
        });
    }
  
    window.addEventListener('storage', checkUserData)
  
    return () => {
      window.removeEventListener('storage', checkUserData)
    }
  }, [])

  useEffect(() => {
    
  }, [login]);
  return login ? <Outlet /> : <Navigate exact to={`/login`} />;
};

export default PrivateRoute;
