import { ApiHost } from '../Config/Config';

let canSet = true;

export function handleResponseAndCheck(response) {
    return response.text().then(async text => {
        let data = text && JSON.parse(text);

        if (!response.ok) {
            if (data.statusCode == 401) {
                if(canSet){
                    canSet = false;
                    let res2 = await fetch(ApiHost + '/auth/admin/token', { method: 'GET', headers: { 'Content-Type': 'application/json', ...bearerRefreshAuthHeader() } })
                    
                    let data2 = await res2.json();

                    if(res2.ok){
                        localStorage.setItem('token', data2.access_token);

                        data = {error: 'Try again'}
                    }else{
                        localStorage.removeItem('token');
                        localStorage.removeItem('refresh_token');
                        localStorage.removeItem('user');

                        data = {...data, error: data2.message.toString()};
                    }
                    canSet = true;
                }else
                    data = {error: 'Try again'}
            }else{
                data = {...data, error: data.message.toString()};
            }
        }

        return data;
    });
}

export function handleResponse(response) {
    return response.text().then(text => {
        const data = text && JSON.parse(text);
        return data;
    });
}

export function bearerAuthHeader() {
    const currentUser = localStorage.getItem('token');
    if (currentUser) {
        return { Authorization: `Bearer ${currentUser}` };
    } else {
        return {};
    }
}

export function bearerRefreshAuthHeader() {
    const currentUser = localStorage.getItem('refresh_token');
    if (currentUser) {
        return { Authorization: `Bearer ${currentUser}` };
    } else {
        return {};
    }
}