import React, { Fragment, useState, useEffect } from 'react';
import { Form, FormGroup, Input, Label } from 'reactstrap';
import {useNavigate} from 'react-router-dom'
import { Btn, H4, P } from '../../../AbstractElements';
import { Password, SignIn, RememberPassword, Email } from '../../../Constant';
import { ToastContainer, toast } from "react-toastify";
import { useCookies } from "react-cookie";

import { ApiHost } from '../../../Config/Config';
import { handleResponseAndCheck } from '../../../Services/fack.backend';

const LoginTab = () => {
  const history = useNavigate();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [remember, setRemember] = useState(false);
  const [togglePassword, setTogglePassword] = useState(false);

  const loginWithJwt = (e) => {
    e.preventDefault();
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json'},
      body: JSON.stringify({ email, password, remember }),
    };

    return fetch(ApiHost + '/auth/admin', requestOptions)
      .then(handleResponseAndCheck)
      .then((res) => {
        if(!res.error){
          localStorage.setItem('token', res.access_token);
          localStorage.setItem('refresh_token', res.refresh_token);
          localStorage.setItem('user', JSON.stringify(res.user));
          history(`/dashboard`);
          toast.success("Successfully logged in!..");
        }else{
          toast.error("You enter wrong password or email!..");
        }
      });
  };

  useEffect(()=>{
    const jwt_token = localStorage.getItem('token');

    if(jwt_token)
      history(`/dashboard`);
  });

  return (
    <Fragment>
      <Form className='theme-form' onSubmit={(e) => loginWithJwt(e)}>
        <H4>{SignIn}</H4>
        <P>{'Enter your email & password to login'}</P>
        <FormGroup>
          <Label className='col-form-label'>{Email}</Label>
          <Input className='form-control' type='email' required={true} onChange={(e) => setEmail(e.target.value)} value={email} />
        </FormGroup>
        <FormGroup className='position-relative'>
          <Label className='col-form-label'>{Password}</Label>
          <div className='position-relative'>
            <Input className='form-control' type={togglePassword ? 'text' : 'password'} required={true} onChange={(e) => setPassword(e.target.value)} value={password} />
            <div className='show-hide' onClick={() => setTogglePassword(!togglePassword)}>
              <span className={togglePassword ? '' : 'show'}></span>
            </div>
          </div>
        </FormGroup>
        <div className='position-relative form-group mb-0'>
          <div className='checkbox'>
            <Input id='checkbox1' type='checkbox' checked={remember} onChange={(e)=>setRemember(e.currentTarget.checked)} />
            <Label className='text-muted' for='checkbox1'>
              {RememberPassword}
            </Label>
          </div>
          {/* <a className='link' href='#javascript'>
            {ForgotPassword}
          </a> */}
          <Btn attrBtn={{ color: 'primary', className: 'd-block w-100 mt-2 mb-2', type: "submit"}}>{SignIn}</Btn>
        </div>
      </Form>
      <ToastContainer />
    </Fragment>
  );
};

export default LoginTab;
