import React, { Fragment, useState, useEffect } from 'react';
import { Container, Row, Col, Card, CardBody, CardHeader, FormGroup, Label } from 'reactstrap';
import { Breadcrumbs, Btn, H5 } from '../../../../AbstractElements';
import DataTableComponent from './DataTableComponent';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import PaymentsSideBar from './OrdersSideBar';
import PaymentsExportModal from './ModalPaymentsExport';
import { ArrowUp } from 'react-feather';
import { Building } from '../../../../Constant';

const OrdersDataTable = () => {
  const [bookings, setBookings] = useState([]);
  const [filter, setFilter] = useState({});
  const [toggleModal, setToggleModal] = useState(false);

  const onChangeFilter = (name, value) => {
    let _filter = {...filter};
    if(value)
      _filter[name] = value;
    else
      delete _filter[name];

    setFilter(_filter);
  }

  const getData = () =>{
    fetch(`${ApiHost}/api/users/orders/?${Object.keys(filter).map((key)=>`${key}=${filter[key]}`).join('&')}`, {method: 'GET', headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()}})
      .then(handleResponseAndCheck)
      .then((res) => {
          if(!res.error){
            setBookings(res);
          }else{
            toast.error(res.error);
          }
      });
  }

  useEffect(()=>{
    const timeOutId = setTimeout(() => getData(), 400);
    return () => clearTimeout(timeOutId);
  }, [filter]);

  return (
    <Fragment>
      <Breadcrumbs parent="Orders" title="Orders Table" mainTitle="Orders Table" />
      <Container fluid={true} className='email-wrap bookmark-wrap todo-wrap'>
        <Row>
          <PaymentsSideBar setStatus={(status)=>onChangeFilter('status', status)} />
          <Col xl='10' className='xl-70 box-col-12'>
            <Card>
              <CardHeader className='d-flex align-items-center justify-content-between'>
                <Row className='w-100 align-items-center'>
                  <Col md="6">
                    <H5>{'Orders'}</H5>
                  </Col>
                  {/* <Col md="6">
                    <Btn attrBtn={{ color: 'primary d-flex align-items-center ml-auto', onClick: ()=>setToggleModal(!toggleModal) }}>
                      <ArrowUp style={{ width: '18px', height: '18px' }} className='me-2' /> {'Export as CSV'}
                    </Btn>
                  </Col> */}
                  <hr md="12" className='mt-4 mb-4'/>
                </Row>
              </CardHeader>
              <CardBody>
                <div className='todo'>
                  <div className='todo-list-wrapper'>
                    <div className='todo-list-container todo-list-footer'>
                      <DataTableComponent data={bookings} setBuildingId={(id)=>onChangeFilter('buildingId', id)} />
                    </div>
                  </div>
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
      <PaymentsExportModal value={toggleModal} addToggle={()=>setToggleModal(!toggleModal)} />
    </Fragment>
  );
};

export default OrdersDataTable;