import React, { Fragment, useCallback, useContext, useEffect, useState } from 'react';
import { NoBookmarksFound, NoServiceCategoriesFound } from '../../../../Constant';
import { Row, Card, Col, CardBody, FormGroup, Label, } from 'reactstrap';
import SweetAlert from 'sweetalert2';
import { Btn, H4, H6, Image, LI, P, UL } from '../../../../AbstractElements';
import { ApiHost } from '../../../../Config/Config';
import CoinContext from '../../../../_helper/Coin';
import { handleResponseAndCheck, bearerAuthHeader } from '../../../../Services/fack.backend';
import HeaderCard from '../../../Common/Component/HeaderCard';
import { toast } from 'react-toastify';
import DataTable from 'react-data-table-component';
import { pairsTableToAndFromColumns } from '../../../../Data/Table/Defaultdata';

const PairsTable = ({ data, setFilter, coinToTypes }) => {
    const { updateCoins, togglePair, setEditPairId } = useContext(CoinContext);

    const [selectedRows, setSelectedRows] = useState([]);
    const [toggleDelet, setToggleDelet] = useState(false);
    const [dataToUpdate, setDataToUpdate] = useState({});

    const handleRowSelected = useCallback(state => {
        setSelectedRows(state.selectedRows);
    }, []);

    const handleDelete = () => {
        SweetAlert.fire({
            title: 'Are you sure?',
            text: 'Once deleted, you will not be able to recover this data!',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Ok',
            cancelButtonText: 'cancel',
            reverseButtons: true,
        }).then(async (result) => {
            if (result.value) {
                for (let i = 0; i < selectedRows.length; i++) {
                    let id = selectedRows[i].id;

                    await fetch(ApiHost + '/api/coins/pairs/' + id, { method: 'DELETE', headers: { 'Content-Type': 'application/json', ...bearerAuthHeader() } })
                        .then(handleResponseAndCheck)
                        .then((res) => {
                            if (res.error) {
                                toast.error(res.error);
                            }
                        });
                }
                setToggleDelet(!toggleDelet);
                updateCoins();
                setSelectedRows('');
            }
        });
    };

    useEffect(() => {
        const timeOutId = setTimeout(() => {
            if (dataToUpdate.pairId) {
                const requestOptions = {
                    method: 'PUT',
                    headers: { 'Content-Type': 'application/json', ...bearerAuthHeader() },
                    body: JSON.stringify(dataToUpdate.filter),
                };

                fetch(ApiHost + '/api/coins/pairs/' + dataToUpdate.pairId, requestOptions)
                    .then(handleResponseAndCheck)
                    .then((res) => {
                        if (!res.error) {
                            updateCoins();
                            toast.success('Updated!');
                        } else {
                            toast.error(res.error);
                        }
                    });
            }
        }, 500);
        return () => clearTimeout(timeOutId);
    }, [togglePair, dataToUpdate]);

    return (
        <Fragment>
            <Row className='mb-4'>
                <FormGroup className="col-3" >
                    <Label style={{display: 'block', textAlign: 'left'}}>From</Label>
                    <select className=" form-control digits" onChange={(e) => setFilter('fromId', e.currentTarget.value)}>
                        <option value="">All</option>
                        {coinToTypes.map((coin) => {
                            return <option key={coin.id} value={coin.id}>{`${coin.name_en}`}</option>
                        })}
                    </select>
                </FormGroup>
                <FormGroup className="col-3" >
                    <Label style={{display: 'block', textAlign: 'left'}}>To</Label>
                    <select className=" form-control digits" onChange={(e) => setFilter('toId', e.currentTarget.value)}>
                        <option value="">All</option>
                        {coinToTypes.map((coin) => {
                            return <option key={coin.id} value={coin.id}>{`${coin.name_en}`}</option>
                        })}
                    </select>
                </FormGroup>
            </Row>
            <Row>
                <Col lg='12' xxl='12' className='box-col-6 debit-card'>
                    <Card className='height-equal'>
                        <CardBody>
                            {(selectedRows.length !== 0) &&
                                <div className={`d-flex align-items-center justify-content-between bg-light-info p-2`}>
                                    <H4 attrH4={{ className: 'text-muted m-0' }}>Delet Selected Data..!</H4>
                                    <Btn attrBtn={{ color: 'danger', onClick: () => handleDelete() }}>Delete</Btn>
                                </div>
                            }
                            <DataTable
                                data={data}
                                columns={pairsTableToAndFromColumns((pairId, payload) => setDataToUpdate({ pairId, filter: payload }), setEditPairId)}
                                striped={true}
                                center={true}
                                pagination
                                selectableRows
                                onSelectedRowsChange={handleRowSelected}
                                clearSelectedRows={toggleDelet}
                            />
                        </CardBody>
                    </Card>
                </Col>

            </Row>
        </Fragment>
    )
};
export default PairsTable;