export const pairsTableColumns = (updateData, setEditPairId) => [
    {
        name: 'Pair Id',
        selector: row => row.id,
        sortable: true,
        center: false,
    },
    {
        name: 'To',
        selector: row => (<Link to={'/coins/' + row.to.coinId + '/' + row.to.id}>{row.to.name_en}</Link>),
        sortable: false,
        center: true,
    },
    {
        name: 'Rate',
        selector: row => (<input
            className="form-control"
            style={{width: '4vw', display: 'inline-block', marginRight: '1vw'}}
            type="text"
            autoComplete="off"
            name='rate'
            defaultValue={row.rate}
            onChange={(e)=>updateData(row.id, { rate: e.currentTarget.value })}
            required
        />),
        sortable: true,
        center: true,
    },
    {
        name: 'Is Active',
        selector: row => (<Media body className={'text-end icon-state switch-outline'}>
            <Label className="switch">
                <input type="checkbox" defaultChecked={row.isActive} onChange={(e)=>updateData(row.id, { isActive: e.currentTarget.checked })} /><span className={'switch-state bg-success'}></span>
            </Label>
        </Media>),
        sortable: false,
        center: false,
    },
    {
        name: 'Show',
        selector: row => (<div className='btn btn-outline-warning btn-xs' onClick={()=>setEditPairId(row.id)}>Show</div>),
        sortable: false,
        center: false,
    }
];

