import React, { Fragment, useContext } from 'react';
import { useForm } from 'react-hook-form';
import { Form, FormGroup, Label, Button, Modal, ModalBody, ModalHeader, Row } from 'reactstrap';
import { Btn } from '../../../../AbstractElements';
import { AddService, Address, Cancel, City, Image, Country, Description, Name, Save, Categories, Price, Type} from '../../../../Constant';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import { useParams } from 'react-router';

const AddCoinWithType = (props) => {
    const {id} = useParams();
    const { register, handleSubmit, formState: { errors }, reset } = useForm();
    const onSubmit = (data) => {
        if (data !== '') {
            const requestOptions = {
                method: 'POST',
                headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()},
                body: JSON.stringify({...data, coinId: id}),
            };
        
            return fetch(ApiHost + '/api/coins/types', requestOptions)
                .then(handleResponseAndCheck)
                .then((res) => {
                    if(!res.error){
                        reset();
                        props.update();
                        props.addToggle();
                    }else{
                        toast.error(res.error);
                    }
                });
        } else {
            errors.showMessages();
        }
    };
    return (
        <Fragment>
            <Modal className="modal-bookmark" isOpen={props.value} toggle={props.addToggle} size="lg">
                <ModalHeader>
                    {'Add Coin with Type'}
                    <Button color="transparent" className="btn-close float-end" onClick={props.addToggle} type="button" data-bs-dismiss="modal" aria-label="Close">
                    </Button>
                </ModalHeader>
                <ModalBody>
                    <Form
                        className="form-bookmark needs-validation"
                        onSubmit={handleSubmit(onSubmit)} >
                        <div className="form-row">
                            <FormGroup className="col-md-12">
                                <Row>
                                    <FormGroup className="col-md-4">
                                        <Label>{Name + ' EN'}</Label>
                                        <input
                                            className="form-control"
                                            type="text"
                                            autoComplete="off"
                                            {...register('name_en', { required: true })}
                                        />
                                        <span style={{ color: 'red' }}>
                                            {errors.name_en && 'Name EN is required'}
                                        </span>
                                    </FormGroup>
                                    <FormGroup className="col-md-4">
                                        <Label>{Name + ' AM'}</Label>
                                        <input
                                            className="form-control"
                                            type="text"
                                            autoComplete="off"
                                            {...register('name_am', { required: true })}
                                        />
                                        <span style={{ color: 'red' }}>
                                            {errors.name_am && 'Name AM is required'}
                                        </span>
                                    </FormGroup>
                                    <FormGroup className="col-md-4">
                                        <Label>{Name + ' RU'}</Label>
                                        <input
                                            className="form-control"
                                            type="text"
                                            autoComplete="off"
                                            {...register('name_ru', { required: true })}
                                        />
                                        <span style={{ color: 'red' }}>
                                            {errors.name_ru && 'Name RU is required'}
                                        </span>
                                    </FormGroup>
                                    <FormGroup className="col-md-4" >
                                        <Label>{Type}</Label><br />
                                        <select className=" form-control digits" {...register('type', { required: true })} defaultValue={props.types[0]}>
                                            {props.types.map((type, i)=>{
                                                return <option key={i} value={type}>{type}</option>
                                            })}
                                        </select>
                                        <span style={{ color: 'red' }}>
                                            {errors.type && 'Type is required'}
                                        </span>
                                    </FormGroup>
                                    <FormGroup className="col-md-4">
                                        <Label>{'Count'}</Label>
                                        <input
                                            className="form-control"
                                            type="number"
                                            autoComplete="off"
                                            {...register('count', { required: true })}
                                        />
                                        <span style={{ color: 'red' }}>
                                            {errors.count && 'Count is required'}
                                        </span>
                                    </FormGroup>
                                </Row>
                            </FormGroup>
                        </div>
                        <Btn attrBtn={{ color: 'secondary', type: 'submit' }} >{Save}</Btn>&nbsp;&nbsp;
                        <Btn attrBtn={{ color: 'primary', onClick: props.addToggle }} >{Cancel}</Btn>
                    </Form>
                </ModalBody>
            </Modal>
        </Fragment>
    );
};
export default AddCoinWithType;