import React, { Fragment, useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Form, FormGroup, Label } from 'reactstrap';
import { H4, Btn } from '../../../../AbstractElements';
import { Password, Save, AddUser, Position, UsersPositions, Email, Role } from '../../../../Constant';
import { ToastContainer, toast } from "react-toastify";
import { useForm } from "react-hook-form";

import { ApiHost } from '../../../../Config/Config';
import { handleResponse, bearerAuthHeader, handleResponseAndCheck } from '../../../../Services/fack.backend';

const RegisterUser = (props) => {
  const [user, setUser] = useState(JSON.parse(localStorage.getItem('user')));
  const { register, handleSubmit, reset, formState: { errors } } = useForm();

  const onEditSubmit = (data) => {
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()},
      body: JSON.stringify(data),
    };

    return fetch(ApiHost + '/api/admins', requestOptions)
      .then(handleResponseAndCheck)
      .then((res) => {
        if(!res.error){
            reset();
            props.render();
            toast.success("User was added!");
        }else{
          toast.error(res.error);
        }
      });
  };

  useEffect(()=>{
    if(!user)
      setUser(JSON.parse(localStorage.getItem('user')));
  });

  return (
    <Fragment>
      <Card>
        <CardHeader>
          <H4 attrH4={{ className: 'card-title mb-0' }}>{AddUser}</H4>
          <div className='card-options'>
            <a className='card-options-collapse' href='#javascript'>
              <i className='fe fe-chevron-up'></i>
            </a>
            <a className='card-options-remove' href='#javascript'>
              <i className='fe fe-x'></i>
            </a>
          </div>
        </CardHeader>
        <CardBody>
          <Form  onSubmit={handleSubmit((data) => onEditSubmit(data))}>
            <FormGroup className='mb-3'>
              <Label className='form-label'>{Email}</Label>
              <input className='form-control' type='email' placeholder='Email' {...register("email", { required: true })}  />
              <span style={{ color: "red" }}>{errors.email && 'Email is required'} </span>
            </FormGroup>
            <FormGroup><Label className="form-label">{Role}</Label>
                <select type="select" {...register("role", { required: true })} className="form-control">
                    {UsersPositions.map((item, i) =>
                        <option key={i} value={item.toLocaleLowerCase()} disabled={i === 0?true:false}>{item}</option>
                    )}
                </select>
                <span style={{ color: "red" }}>{errors.position && 'Position is required'} </span>
            </FormGroup>
            <FormGroup className='mb-3'>
              <Label className='form-label'>{Password}</Label>
              <input className='form-control' type='password' placeholder='Password' {...register("password", { required: true })}  />
              <span style={{ color: "red" }}>{errors.password && 'New Password is required'} </span>
            </FormGroup>
            <div className='form-footer'>
              <Btn attrBtn={{ className: 'btn-block', color: 'primary', type: 'submit' }}>{Save}</Btn>
            </div>
          </Form>
        </CardBody>
      </Card>
      <ToastContainer />
    </Fragment>
  );
};
export default RegisterUser;
