import React, { useState } from 'react';
import Context from './index';

const CoinProvider = (props) => {
  const [toggleCoin, setToggleCoin] = useState(false);
  const [togglePair, setTogglePair] = useState(false);
  const [editPairId, setEditPairId] = useState(null);
  const [categories, setCategories] = useState([]);
  const [activeTabss, setActiveTabss] = useState('1');

  const updateCoins = () =>{
    setToggleCoin(!toggleCoin);
  }

  const updatePairs = () =>{
    setTogglePair(!togglePair);
  }


  return (
    <Context.Provider
      value={{
        ...props,
        activeTabss,
        updateCoins,
        toggleCoin,
        updatePairs,
        setActiveTabss,
        categories,
        setCategories,
        togglePair,
        editPairId,
        setEditPairId,
      }}
    >
      {props.children}
    </Context.Provider>
  );
};

export default CoinProvider;
