import React, { Fragment, useEffect, useState } from 'react';
import { Card, CardHeader } from 'reactstrap';
import { Btn, H4 } from '../../../../AbstractElements';
import { Delete, UsersTableHeader, UsersTableTitle } from '../../../../Constant';

import { ApiHost } from '../../../../Config/Config';
import { bearerAuthHeader, handleResponseAndCheck } from '../../../../Services/fack.backend';
import { toast } from 'react-toastify';

const AdminTable = (props) => {
  const [data, setData] = useState([]);
  const [isMounted, setIsMounted] = useState(false);

  const handleDelete = (id) =>{
    fetch(ApiHost + '/api/admins/' + id, {method: "DELETE", headers: {...bearerAuthHeader()}})
    .then(handleResponseAndCheck)
    .then((res) => {
      if(res.success){
        setIsMounted(false);
        toast.success("User deleted!");
      }
    });
  }

  useEffect(() => {
    setIsMounted(true);
    fetch(ApiHost + '/api/admins', {method: "GET", headers: {...bearerAuthHeader()}})
    .then(handleResponseAndCheck)
    .then((res) => {
      if(isMounted){
        setData(res)
      }
    });
    return () => {
      setIsMounted(false);
    };
  }, [isMounted, props.toggle]);

  return (
    <Fragment>
      <Card>
        <CardHeader>
          <H4 attrH4={{ className: 'card-title mb-0' }}>{UsersTableTitle}</H4>
          <div className='card-options'>
            <a className='card-options-collapse' href='#javascript' data-toggle='card-collapse'>
              <i className='fe fe-chevron-up'></i>
            </a>
            <a className='card-options-remove' href='#javascript' data-toggle='card-remove'>
              <i className='fe fe-x'></i>
            </a>
          </div>
        </CardHeader>
        <div className='table-responsive edit-table'>
          <table className='table card-table table-vcenter text-nowrap'>
            <thead>
              <tr>
                {UsersTableHeader.map((items, i) => (
                  <th key={i}>{items}</th>
                ))}
              </tr>
            </thead>
            <tbody>
              {data.map((user, i) => (
                <tr key={i}>
                  <td>{user.email}</td>
                  <td>
                    <span className='status-icon bg-success'></span>
                    {user.role}
                  </td>
                  <td className='text-end'>
                    <Btn attrBtn={{ color: 'danger', size: 'sm', onDoubleClick: ()=>handleDelete(user.id) }}>
                      <i className='fa fa-trash'></i> {Delete}
                    </Btn>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </Card>
    </Fragment>
  );
};

export default AdminTable;