import React, { Fragment, useState, useEffect } from 'react';
import { Col, Container, Row } from 'reactstrap';
import { Breadcrumbs } from '../../../../AbstractElements';
import UserInfo from './UserInfo';
import PaymentInfoForm from './Info';
import { useParams } from 'react-router';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import ApartmentInfo from './ApartmentInfo';

const OrderInfo = () => {
  const { id } = useParams();
  const [order, setOrder] = useState({ user: {}, place: { }, pair: { to: {}, from: {} } });
  const [update, setUpdate] = useState(false);

  const getData = () =>{
    fetch(`${ApiHost}/api/users/orders/${id}`, {method: 'GET', headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()}})
      .then(handleResponseAndCheck)
      .then((res) => {
          if(!res.error){
            setOrder(res);
          }else{
            toast.error(res.error);
          }
      });
  }

  useEffect(()=>{
    getData();
  }, [update]);

  return (
    <Fragment>
      <Breadcrumbs mainTitle='Order Information' parent='Orders' title='Order Information' />
      <Container fluid={true}>
        <div className='edit-profile'>
          <Row>
            <Col xl='4'>
              <PaymentInfoForm order={order} update={()=>setUpdate(!update)} />
            </Col>
            {order.apartment && <Col xl='4'>
              <ApartmentInfo data={order.apartment} />
            </Col>}
            <Col xl='4'>
              <UserInfo data={order.user} />
            </Col>
          </Row>
        </div>
      </Container>
    </Fragment>
  );
};
export default OrderInfo;
