import React, { Fragment, useState, useEffect, useCallback } from 'react';
import { Container, Row, Col, Card, CardBody, FormGroup, Label } from 'reactstrap';
import { Breadcrumbs, Btn } from '../../../../AbstractElements';
import HeaderCard from '../../../Common/Component/HeaderCard';
import UserTableComponent from './UserTableComponent';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";

const UserDataTables = () => {
  const [users, setUsers] = useState([]);
  const [updateData, setUpdateData] = useState(false);
  const [query, setQuery] = useState("");

  const getData = (query) =>{
    fetch(`${ApiHost}/api/users/${query ? '?query=' + query : ''}`, {method: 'GET', headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()}})
      .then(handleResponseAndCheck)
      .then((res) => {
          if(!res.error){
            setUsers(res);
          }else{
            toast.error(res.error);
          }
      });
  }

  useEffect(()=>{
    getData();
  }, [updateData]);

  useEffect(() => {
    const timeOutId = setTimeout(() => getData(query), 500);
    return () => clearTimeout(timeOutId);
  }, [query]);

  return (
    <Fragment>
      <Breadcrumbs parent="Users" title="Users Table" mainTitle="Users Table" />
      <Container fluid={true}>
        <Row>
          {/* <UsersSideBar sendPushNot={addToggle} sendWarningEmail={addEmailToggle} /> */}
          <Col xl='12' className='xl-70 box-col-12'>
            <Card>
              {/* <HeaderCard title="" span1={
                <Row className='align-items-center'>
                  <FormGroup className="col-md-3">
                      <Label>Search</Label>
                      <input name="query" type='text' className="form-control" autoComplete="off" onChange={(e)=>setQuery(e.currentTarget.value)} />
                  </FormGroup>
                </Row>
              }/> */}
              <CardBody>
                <UserTableComponent update={()=>setUpdateData(!updateData)} data={users} />
              </CardBody>
            </Card>
          </Col>
        </Row>
      </Container>
    </Fragment>
  );

};

export default UserDataTables;