import React, { Fragment, useCallback, useContext, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Form, FormGroup, Label, Button, Modal, ModalBody, ModalHeader, Row } from 'reactstrap';
import { Btn, H4 } from '../../../../AbstractElements';
import { Cancel, Name, Priority, Save } from '../../../../Constant';
import {handleResponseAndCheck, bearerAuthHeader} from '../../../../Services/fack.backend';
import { ApiHost } from '../../../../Config/Config';
import { toast } from "react-toastify";
import CoinContext from '../../../../_helper/Coin';
import DataTable from 'react-data-table-component';
import { intervalColumns } from '../../../../Data/Table/Defaultdata';
import SweetAlert from 'sweetalert2';

const PairModal = () => {
    const { setEditPairId, editPairId } = useContext(CoinContext);
    const { register, handleSubmit, formState: { errors }, reset } = useForm();
    const [intervals, setIntervals] = useState([]);
    const [update, setUpdate] = useState(false);
    const [selectedRows, setSelectedRows] = useState([]);
    const [toggleDelet, setToggleDelet] = useState(false);
    const [dataToUpdate, setDataToUpdate] = useState({ });

    const handleRowSelected = useCallback(state => {
        setSelectedRows(state.selectedRows);
    }, []);

    const handleDelete = () => {
        SweetAlert.fire({
            title: 'Are you sure?',
            text: 'Once deleted, you will not be able to recover this data!',
            icon: 'warning',
            showCancelButton: true,
            confirmButtonText: 'Ok',
            cancelButtonText: 'cancel',
            reverseButtons: true,
        }).then(async (result) => {
            if (result.value) {
                for(let i = 0; i < selectedRows.length; i++){
                    let id = selectedRows[i].id;

                    await fetch(ApiHost + '/api/coins/pairs/' + editPairId + '/intervals/' + id, {method: 'DELETE', headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()}})
                    .then(handleResponseAndCheck)
                    .then((res) => {
                        if(res.error){
                            toast.error(res.error);
                        }
                    });
                }
                setToggleDelet(!toggleDelet);
                setUpdate(!update);
                setSelectedRows('');
            }
        });
    };

    const addToggle=()=>{
        setEditPairId(null);
        setIntervals([]);
    }

    useEffect(()=>{
        if(editPairId){
            const requestOptions = {
                method: 'GET',
                headers: {'Content-Type': 'application/json', ...bearerAuthHeader()},
            };
        
            return fetch(ApiHost + '/api/coins/pairs/' + editPairId + '/intervals', requestOptions)
                .then(handleResponseAndCheck)
                .then((res) => {
                    if(!res.error){
                        setIntervals(res);
                    }else{
                        toast.error(res.error);
                    }
                });
        }
    }, [editPairId, update])

    const onSubmit1 = (data) => {
        if (data !== '') {
            if(!data.from.length)
                data.from = null;

            if(!data.to.length)
                data.to = null;

            const requestOptions = {
                method: 'POST',
                headers: {'Content-Type': 'application/json', ...bearerAuthHeader()},
                body: JSON.stringify(data),
            };
        
            return fetch(ApiHost + '/api/coins/pairs/' + editPairId + '/intervals', requestOptions)
                .then(handleResponseAndCheck)
                .then((res) => {
                    if(!res.error){
                        reset();
                        setUpdate(!update);
                        toast.success('Added!');
                    }else{
                        toast.error(res.error);
                    }
                });
        } else {
            errors.showMessages();
        }
    };

    useEffect(()=>{
        const timeOutId = setTimeout(()=> {
            if(dataToUpdate.intervalId){
                const requestOptions = {
                    method: 'PUT',
                    headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()},
                    body: JSON.stringify(dataToUpdate.filter),
                };
        
                fetch(ApiHost + '/api/coins/pairs/' + editPairId + '/intervals/' + dataToUpdate.intervalId, requestOptions)
                    .then(handleResponseAndCheck)
                    .then((res) => {
                        if(!res.error){
                            setUpdate(!update);
                            toast.success('Updated!');
                            setDataToUpdate({});
                        }else{
                            toast.error(res.error);
                        }
                    });
            }
        }, 500);
        return () => clearTimeout(timeOutId);
    }, [dataToUpdate]);

    return (
        <Fragment>
            <Modal className="modal-bookmark" isOpen={editPairId != null} toggle={addToggle} size="lg">
                <ModalHeader>
                    {"Pair: " + editPairId + " Intervals"}
                    <Button color="transparent" className="btn-close float-end" onClick={addToggle} type="button" data-bs-dismiss="modal" aria-label="Close">
                    </Button>
                </ModalHeader>
                <ModalBody>
                    <Form
                        className="form-bookmark needs-validation mb-3"
                        onSubmit={handleSubmit(onSubmit1)} >
                        <div className="form-row">
                            <FormGroup className="col-md-12">
                                <Row>
                                    <FormGroup className="col-md-4">
                                        <Label>{"From"}</Label>
                                        <input
                                            className="form-control"
                                            type="number"
                                            autoComplete="off"
                                            {...register('from', { required: false })}
                                        />
                                    </FormGroup>
                                    <FormGroup className="col-md-4">
                                        <Label>{"To"}</Label>
                                        <input
                                            className="form-control"
                                            type="number"
                                            autoComplete="off"
                                            {...register('to', { required: false })}
                                        />
                                    </FormGroup>
                                    <FormGroup className="col-md-4">
                                        <Label>{"Fixed Price"}</Label>
                                        <input
                                            className="form-control"
                                            type="number"
                                            autoComplete="off"
                                            {...register('fixedPrice', { required: true })}
                                        />
                                        <span style={{ color: 'red' }}>
                                            {errors.fixedPrice && 'Fixed Price is required'}
                                        </span>
                                    </FormGroup>
                                    <FormGroup className="col-md-4">
                                        <Label>{"Percent"}</Label>
                                        <input
                                            className="form-control"
                                            type="number"
                                            step="0.01"
                                            autoComplete="off"
                                            {...register('percent', { required: true })}
                                        />
                                        <span style={{ color: 'red' }}>
                                            {errors.percent && 'Percent is required'}
                                        </span>
                                    </FormGroup>
                                </Row>
                            </FormGroup>
                        </div>
                        <Btn attrBtn={{ color: 'secondary', type: 'submit' }} >{Save}</Btn>&nbsp;&nbsp;
                        <Btn attrBtn={{ color: 'primary', onClick: addToggle }} >{Cancel}</Btn>
                    </Form>
                    {(selectedRows.length !== 0) &&
                        <div className={`d-flex align-items-center justify-content-between bg-light-info p-2`}>
                            <H4 attrH4={{ className: 'text-muted m-0' }}>Delet Selected Data..!</H4>
                            <Btn attrBtn={{ color: 'danger', onClick: () => handleDelete() }}>Delete</Btn>
                        </div>
                    }
                    <DataTable
                        data={intervals}
                        columns={intervalColumns((intervalId, payload)=>setDataToUpdate({intervalId, filter: payload}))}
                        striped={true}
                        center={true}
                        pagination
                        selectableRows
                        onSelectedRowsChange={handleRowSelected}
                        clearSelectedRows={toggleDelet}
                    />
                </ModalBody>
            </Modal>
        </Fragment>
    );
};
export default PairModal;