import React, { Fragment } from "react";
import { Btn, H4 } from "../../../../AbstractElements";
import { useForm } from "react-hook-form";
import { Row, Col, CardHeader, CardBody, CardFooter, Form, FormGroup, Label } from 'reactstrap'
import { ChangePassword, ConfirmPassword, EnterPassword, NewPassword, SaveChanges } from '../../../../Constant';
import { ToastContainer, toast } from "react-toastify";

import { ApiHost } from '../../../../Config/Config';
import { bearerAuthHeader, handleResponseAndCheck } from '../../../../Services/fack.backend';

const EditMyProfile = () => {
    const { register, handleSubmit, reset, formState: { errors } } = useForm();

    const onEditSubmit = (data) => {
      if(data.new_password !== data.confirm_new_password){
        toast.error("Passwords don't match");
      }else{
        const requestOptions = {
          method: 'PUT',
          headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()},
          body: JSON.stringify(data),
        };
    
        fetch(ApiHost + '/api/admins/new-password', requestOptions)
          .then(handleResponseAndCheck)
          .then((res) => {
            if(!res.error){
                reset();
                toast.success("Password was changed");
            }else{
                toast.error(res.error);
            }
          });
      }
    };


    return (
        <Fragment>
            <Form className="card"  onSubmit={handleSubmit((data) => onEditSubmit(data))}>
                <CardHeader>
                    <H4 attrH4={{ className: "card-title mb-0" }}>{ChangePassword}</H4>
                    <div className="card-options">
                        <a className="card-options-collapse" href="#javascript">
                            <i className="fe fe-chevron-up"></i>
                        </a>
                        <a className="card-options-remove" href="#javascript">
                            <i className="fe fe-x"></i>
                        </a>
                    </div>
                </CardHeader>
                <CardBody>
                    <Row>
                        <Col sm="6" md="6">
                            <FormGroup><Label className="form-label">{"Old Password"}</Label>
                                <input className="form-control" type="password" placeholder="Old Password" {...register("old_password", { required: true })} /><span style={{ color: "red" }}>{errors.old_password && 'Old Password is required'} </span>
                            </FormGroup>
                        </Col>
                        <Col sm="6" md="6">
                            <FormGroup><Label className="form-label">{NewPassword}</Label>
                                <input className="form-control" type="password" placeholder="New Password" {...register("new_password", { required: true })} /><span style={{ color: "red" }}>{errors.new_password && 'New Password is required'} </span>
                            </FormGroup>
                        </Col>
                        <Col sm="6" md="6">
                            <FormGroup><Label className="form-label">{ConfirmPassword}</Label>
                                <input className="form-control" type="password" placeholder="Confirm New Password" {...register("confirm_new_password", { required: true })} /><span style={{ color: "red" }}>{errors.confirm_new_password && 'New Password is required'} </span>
                            </FormGroup>
                        </Col>
                    </Row>
                </CardBody>
                <CardFooter className="text-end">
                    <Btn attrBtn={{ color: "primary", type: "submit" }} >{SaveChanges}</Btn>
                </CardFooter>
            </Form>
            <ToastContainer />
        </Fragment>
    )
}
export default EditMyProfile