import React, { Fragment, useCallback, useContext, useState } from 'react';
import { Plus } from 'react-feather';
import { Nav, NavItem } from 'reactstrap';
import { Btn } from '../../../../AbstractElements';
import { Views } from '../../../../Constant';
import CoinModal from './ModalCoin';
import CoinContext from '../../../../_helper/Coin';

const NavTab = () => {
  const { setActiveTabss } = useContext(CoinContext);
  const [addModal, setAddModal] = useState(false);
  const [addServiceModal, setAddServiceModal] = useState(false);

  const activeTab = '1';

  const addToggle = () => {
    setAddModal(!addModal);
  };

  const addServiceToggle = () => {
    setAddServiceModal(!addServiceModal);
  };

  const onHandleClick = (id) => {
    setActiveTabss(id);
  };

  const addToggleCallback = useCallback((toggle) => {
    setAddModal(toggle);
  });

  const addServiceToggleCallback = useCallback((toggle) => {
    setAddServiceModal(toggle);
  });

  return (
    <Fragment>
      <Nav className='main-menu' role='tablist'>
        <NavItem>
          <Btn attrBtn={{ className: 'btn-mail d-block w-100 badge-light-primary', color: '', onClick: addToggle }}>
            <Plus className='me-2' /> {'New Coin'}
          </Btn>
          <CoinModal value={addModal} addToggle={addToggle} addToggleCallback={addToggleCallback} />
        </NavItem>
        <NavItem>
          <span className='main-title'>{Views}</span>
        </NavItem>
        <NavItem>
          <a className={`pointer ${activeTab === '1' ? 'active' : ''}`} onClick={() => onHandleClick('1')}>
            <span className='title'> {'Coins'}</span>
          </a>
        </NavItem>
        <NavItem>
          <a className={`pointer ${activeTab === '2' ? 'active' : ''}`} onClick={() => onHandleClick('2')}>
            <span className='title'>
              {"Pairs"}
            </span>
          </a>
        </NavItem>
      </Nav>
    </Fragment>
  );
};
export default NavTab;
