import React, { Fragment, useEffect, useState } from 'react';
import { Card, CardBody, Col, FormGroup, Label, Row } from 'reactstrap';
import { Btn, Image } from '../../../../AbstractElements';
import HeaderCard from '../../../Common/Component/HeaderCard';
import { ApiHost } from '../../../../Config/Config';
import { handleResponseAndCheck, bearerAuthHeader } from '../../../../Services/fack.backend';
import { toast } from "react-toastify";
import { useForm } from 'react-hook-form';
import { Add, Save } from '../../../../Constant';

const AddPair = ({ coinType, typeOptions, update }) => {
  const [photos, setPhotos] = useState([]);
  const { register, handleSubmit, formState: { errors }, setValue } = useForm();

  useEffect(()=>{
    if(coinType.photos)
      setPhotos(coinType.photos.split(','));
  }, [coinType])

  const handleUpdate = (data) => {
      if (data !== '') {
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/json', ...bearerAuthHeader()},
            body: JSON.stringify(data),
        };

        return fetch(ApiHost + '/api/coins/types/' + coinType.id + '/pairs', requestOptions)
            .then(handleResponseAndCheck)
            .then((res) => {
                if(!res.error){
                  update();
                  toast.success('Added!');
                }else{
                    toast.error(res.error);
                }
            });
      } else {
        errors.showMessages();
      }
  };

  return (
    <Fragment>
        <Col sm='4'>
          <Card>
            <HeaderCard title={'Add Pair'} />
            <CardBody>
              <form onSubmit={handleSubmit(handleUpdate)}  className='theme-form mega-form mb-4'>
                <Row className='theme-form e-commerce-form'>
                  <FormGroup className="col-md-12" >
                      <Label>{"Coin Pair"}</Label><br />
                      <select className=" form-control digits" {...register('toId', { required: true })}>
                          <option disabled value={''}>- Select -</option>
                          {typeOptions.map((e, i)=>{
                              return <option key={i} value={e.id}>{e.name_en}</option>
                          })}
                      </select>
                      <span style={{ color: 'red' }}>
                          {errors.toId && 'Coin Pair is required'}
                      </span>
                  </FormGroup>
                  <FormGroup className="col-md-12">
                      <Label>{'Rate'}</Label>
                      <input
                          className="form-control"
                          type="number"
                          autoComplete="off"
                          step="0.01"
                          {...register('rate', { required: true })}
                      />
                      <span style={{ color: 'red' }}>
                          {errors.rate && 'Rate is required'}
                      </span>
                  </FormGroup>
                </Row>
                <Btn attrBtn={{ color: 'primary', type: 'submit' }} >{Add}</Btn>
              </form>
            </CardBody>
          </Card>
        </Col>
    </Fragment>
  );
};

export default AddPair;
